module.exports = [[module.id, "/* Importar Fuentes custom */\r\n@font-face{\r\n    font-family: \"yanone\";\r\n    src: url('yanone.ttf');\r\n}\r\n@font-face{\r\n    font-family: \"finger\";\r\n    src: url('finger.ttf');\r\n}\r\n/* Generales */\r\n*{\r\n    margin: 0px;\r\n    padding: 0px;\r\n}\r\nbody{\r\n    font-family: \"yanone\",Arial, Helvetica;\r\n    font-size: 20px;\r\n    background: url('pattern.png');\r\n    background-attachment: fixed; /*para que el fondo se quede fijado*/\r\n}\r\n.clearfix{\r\n    float: none;\r\n    clear:both;\r\n}\r\n/* CABECERA / NAVIGATOR */\r\nheader{\r\n    display: block;\r\n    background-color: rgba(32, 108, 250, 0.9);\r\n    width: 100%;\r\n    height: 90px;\r\n    position: fixed; /* me pone la barra al 100% y no se movera */\r\n    text-transform: uppercase;\r\n    \r\n}\r\nheader ul{\r\n    display: block;\r\n    width: 50%;\r\n    height: inherit;\r\n    margin: 0px auto;\r\n    margin-top: -3px;\r\n}\r\nheader ul li{\r\n    display: inline-block;\r\n    margin: 0px auto;\r\n    text-align: center;\r\n    line-height: 90px;\r\n    \r\n}\r\nheader a{\r\n    display: block;\r\n    padding-right: 20px;\r\n    padding-left: 20px;\r\n    height: inherit;\r\n    text-decoration: none;\r\n    color: white;\r\n    transition: all 300ms;\r\n    margin-left: 5.2px;\r\n    margin-right: 5.2px;\r\n}\r\nheader a:hover,\r\n.actived{\r\n    /*background: rgba(0, 0, 0, 0.8);\r\n    color: silver;*/\r\n    background: rgba(255, 255, 255, 0.5);\r\n    color: black;\r\n    -webkit-transform: scale(1.10, 1.00);\r\n            transform: scale(1.10, 1.00);\r\n}\r\n/* LOGOTIPO */\r\n#logo{\r\n    font-family: \"finger\";\r\n    font-size: 30px;\r\n}\r\n#logo a{\r\n    transition: color 500ms, -webkit-transform 500ms;\r\n    transition: color 500ms, transform 500ms;\r\n    transition: color 500ms, transform 500ms, -webkit-transform 500ms;\r\n}\r\n#logo a:hover{\r\n    background: transparent;\r\n    color: black;\r\n    -webkit-transform: scale(1.50, 1.50);\r\n            transform: scale(1.50, 1.50);\r\n}\r\n/* CONTENIDO */\r\n#content{\r\n    min-height: 600px;\r\n    height: auto;\r\n    width: 75%;\r\n    margin: 0 auto;\r\n    box-shadow: 0px 0px 40px black;\r\n    background: rgba(255, 255, 255, 0.6);\r\n    padding-top: 120px;\r\n    border-left: 1px solid #b7b7b7;\r\n    border-right: 1px solid #b7b7b7;\r\n}\r\n/* FOOTER */\r\nfooter{\r\n    width: 75%;\r\n    margin: 0px auto;\r\n    padding-top: 15px;\r\n    color: rgba(0, 0, 0, 0.7);\r\n    \r\n}\r\n/* Sobre mi */\r\n#title{\r\n    width: 70%;\r\n    margin: 0px auto;\r\n    text-align: center;\r\n    margin-bottom: 70px;\r\n}\r\n#title h1 {\r\n    font-family: \"finger\";\r\n    font-size: 60px;\r\n    border-bottom: 1px solid #ccc;\r\n    padding-bottom: 5px;\r\n}\r\n#title h2 {\r\n    font-size: 25px;\r\n    color: gray;\r\n    margin-top: 5px;\r\n    margin-bottom: 5px;\r\n}\r\n#title p a {\r\n    text-decoration: none;\r\n    color: black;\r\n}\r\narticle.about{\r\n    width: 70%;\r\n    margin: 0px auto;\r\n    margin-bottom: 20px;\r\n    font-size: 23px;\r\n}\r\n.about h2{\r\n    border-bottom: 1px solid #ccc;\r\n    padding-bottom: 5px;\r\n    margin-bottom: 5px;\r\n}\r\n.about p{\r\n    padding-top: 20px;\r\n    padding-bottom: 30px;\r\n    text-align: justify;\r\n}\r\n/* Seccion con Formulario */\r\n.container{\r\n    padding: 50px;\r\n    padding-top: 10px;\r\n}\r\n.container .image{\r\n    float: left;\r\n    width: 49%;\r\n}\r\n.container .image img{\r\n    width: 100%;\r\n}\r\n.container .data{\r\n    float: left;\r\n    width: 47%;\r\n    margin-left: 30px;\r\n}\r\n.container h2 {\r\n    border-bottom: 1px solid #ccc;\r\n    padding-bottom: 10px;\r\n    margin-bottom: 15px;\r\n}\r\nform{\r\n    width: 70%;\r\n}\r\nform label{\r\n    width: 100%;\r\n    display: block;\r\n    margin-top: 10px;\r\n    margin-bottom: 5px;\r\n}\r\nform input[type=\"text\"],\r\nform input[type=\"number\"],\r\nform input[type=\"email\"],\r\nform textarea {\r\n\r\n    width: 40%;\r\n    padding: 5px;\r\n}\r\nform button,\r\nform input[type=\"submit\"],\r\n.button-edit,\r\n.button-delete,\r\n.button-delete-2 {\r\n\r\n    font-size: 17px;\r\n    display: block;\r\n    padding: 10px;\r\n    margin-top: 15px;\r\n    color: white;\r\n    background: rgba(53, 103, 164, 1);\r\n    /*border: 1px solid rgba(13, 67, 133, 1);*/\r\n    cursor: pointer;\r\n}\r\n.button-edit,\r\n.button-delete,\r\n.button-delete-2 {\r\n    width: 40%;\r\n    float: left;\r\n    text-align: center;\r\n    margin-right: 10px;\r\n    text-decoration: none;\r\n}\r\n.button-edit {\r\n    background: darkcyan;\r\n    \r\n}\r\n.button-delete {\r\n    background: darkorange;\r\n    \r\n}\r\n.button-delete-2 {\r\n    background: darkmagenta;\r\n}\r\nform button:hover,\r\nform input[type=\"submit\"]:hover {\r\n\r\n    background: rgba(53, 103, 164, 0.8);\r\n}\r\n.form_error {\r\n    background: red;\r\n    color: white;\r\n    font-size: 18px;\r\n    margin-left: 5px;\r\n}\r\nform input[disabled]{\r\n    opacity: 0.4;\r\n    cursor: not-allowed;\r\n}\r\n.message {\r\n    padding: 5px;\r\n    border: 1px solid #eee;\r\n    width: 50%;\r\n}\r\n.success {\r\n    background: green;\r\n    color: white;\r\n}\r\n.message a{\r\n    color: white;\r\n}\r\n.failed{\r\n    background: red;\r\n    color: white;\r\n}\r\n/* Pagina de Proyectos */\r\n.project{\r\n    list-style: none;\r\n    display: block;\r\n    float: left;\r\n    width: 28%;\r\n    text-align: center;\r\n    margin-left: 20px;\r\n    margin-right: 20px;\r\n    margin-bottom: 30px;\r\n    \r\n}\r\n.project .image:hover{\r\n    border-radius: 10px;\r\n}\r\n.project .image{\r\n    width: 100%;\r\n    height: 150px;\r\n    overflow: hidden;\r\n    \r\n}\r\n.project img{\r\n    width: 100%;\r\n    transition: all 0.9s ease;\r\n    overflow:hidden;\r\n}\r\n.project:hover{\r\n    opacity: 0.8;\r\n    cursor: pointer;\r\n    \r\n}\r\n.project a{\r\n    text-decoration: none;\r\n    color: black;\r\n}\r\n/*\r\n.image:hover img{\r\n    transform:scale(1.25);\r\n    \r\n}\r\n*/\r\n.detail-img .image img{\r\n    border: 1px solid black;\r\n}\r\n.project-img .image:hover img{\r\n    -webkit-transform:scale(1.25);\r\n            transform:scale(1.25);\r\n}\r\n.confirm{\r\n    display: block;\r\n    font-weight: bold;\r\n    margin-top: 20px;\r\n    font-size: large;\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]